<template>
  <v-row class="fill-height">
    <v-col cols="2" class="wrapper">
      <div class="sidebar" >
        <ul class="list-group" >
          <li v-for="value in typeCalendar">
            <v-btn 
              class="sidebar__btn btn-calendar" 
              @click="getCalendar(value)" :color="SelectNow == value ? 'primary' : '' ">
              {{value}}
            </v-btn>
            <v-icon class="delete-cal" @click="deleteCalendar(value)">mdi-delete</v-icon>
          </li>
        </ul>
        <v-btn @click.stop="dialogCalendar = true" class="sidebar__btn btn-add-calendar" color="primary">Добавить календарь</v-btn>
      </div>
    </v-col>
    <v-col>
      <v-sheet height="64">
        <v-toolbar flat color="white">
          <v-btn color="primary" dark @click.stop="dialog = true">
            New Event
          </v-btn>
          <v-btn outlined class="mr-4" @click="setToday">
            Today
          </v-btn>
          <v-btn fab text small @click="prev">
            <v-icon small>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn fab text small @click="next">
            <v-icon small>mdi-chevron-right</v-icon>
          </v-btn>
          <v-toolbar-title>{{ title }}</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-menu bottom right>
            <template v-slot:activator="{ on }">
              <v-btn outlined v-on="on">
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right>mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="type = 'day'">
                <v-list-item-title>Day</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'week'">
                <v-list-item-title>Week</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'month'">
                <v-list-item-title>Month</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = '4day'">
                <v-list-item-title>4 days</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
      </v-sheet>

      <v-dialog v-model="dialog" max-width="500">
        <v-card>
          <v-container>
            <v-form @submit.prevent="addEvent">
              <v-text-field v-model="name" type="text" label="event name (required)"></v-text-field>
              <v-text-field v-model="details" type="text" label="detail"></v-text-field>
              <v-text-field v-model="start" type="date" label="start (required)"></v-text-field>
              <v-text-field v-model="end" type="date" label="end (required)"></v-text-field>
              <v-text-field v-model="color" type="color" label="color (click to open color menu)"></v-text-field>
              <v-btn type="submit" color="primary" class="mr-4" @click.stop="dialog = false">
                create event
              </v-btn>
            </v-form>
          </v-container>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogCalendar" max-width="500">
        <v-card>
          <v-container>
            <v-form @submit.prevent="addCalendar">
              <v-text-field v-model="calendarName" type="text" label="calendar name (required)"></v-text-field>
              <v-text-field v-model="calendarColor" type="color" label="color (click to open color menu)"></v-text-field>
              <v-btn type="submit" color="primary" class="mr-4" @click.stop="dialog = false">
                create event
              </v-btn>
            </v-form>
          </v-container>
        </v-card>
      </v-dialog>

      <!-- <v-dialog v-model="dialogDate" max-width="500">
        <v-card>
          <v-container>
            <v-form @submit.prevent="addEvent">
              <v-text-field v-model="name" type="text" label="event name (required)"></v-text-field>
              <v-text-field v-model="details" type="text" label="detail"></v-text-field>
              <v-text-field v-model="start" type="date" label="start (required)"></v-text-field>
              <v-text-field v-model="end" type="date" label="end (required)"></v-text-field>
              <v-text-field v-model="color" type="color" label="color (click to open color menu)"></v-text-field>
              <v-btn type="submit" color="primary" class="mr-4" @click.stop="dialog = false">
                create event
              </v-btn>
            </v-form>
          </v-container>
        </v-card>
      </v-dialog> -->

<v-sheet height="600">
  <v-calendar
  ref="calendar"
  v-model="focus"
  color="primary"
  :events="events"
  :event-color="getEventColor"
  :event-margin-bottom="3"
  :now="today"
  :type="type"
  @click:event="showEvent"
  @click:more="viewDay"
  @click:date="setDialogDate"
  @change="updateRange"
  ></v-calendar>
  <v-menu
  v-model="selectedOpen"
  :close-on-content-click="false"
  :activator="selectedElement"
  full-width
  offset-x
  >
  <v-card color="grey lighten-4" :width="350" flat>
    <v-toolbar :color="selectedEvent.color" dark>
      <v-btn @click="deleteEvent(selectedEvent.id)" icon>
        <v-icon>mdi-delete</v-icon>
      </v-btn>
      <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
      <div class="flex-grow-1"></div>
    </v-toolbar>

    <v-card-text>
      <form v-if="currentlyEditing !== selectedEvent.id">
        {{ selectedEvent.details }}
      </form>
      <form v-else>
        <textarea-autosize
        v-model="selectedEvent.details"
        type="text"
        style="width: 100%"
        :min-height="100"
        placeholder="add note">
      </textarea-autosize>
    </form>
  </v-card-text>

  <v-card-actions>
    <v-btn text color="secondary" @click="selectedOpen = false">
      close
    </v-btn>
    <v-btn v-if="currentlyEditing !== selectedEvent.id" text @click.prevent="editEvent(selectedEvent)">
      edit
    </v-btn>
    <v-btn text v-else type="submit" @click.prevent="updateEvent(selectedEvent)">
      Save
    </v-btn>
  </v-card-actions>
</v-card>
</v-menu>
</v-sheet>
</v-col>
</v-row>
</template>

<script>
import { db } from '@/main'
import { dbRealTime } from '@/main'

function Random(){
  return Math.random().toString(36).slice(2) 
}
export default {
  data: () => ({
    today: new Date().toISOString().substr(0, 10),
    focus: new Date().toISOString().substr(0, 10),
    type: 'month',
    typeToLabel: {
      month: 'Month',
      week: 'Week',
      day: 'Day',
      '4day': '4 Days',
    },
    dialogCalendar: false,
    calendarName: null,
    calendarColor: '#000',
    name: null,
    details: null,
    start: '',
    end: null,
    color: '#000', // default event color
    currentlyEditing: null,
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    dialog: false,
    dialogDate: false,
    typeCalendar: [],
    SelectNow: ''
  }),
  mounted () {
    this.getCalendar()
  },
  created(){
    
  },
  computed: {
    title () {
      const { start, end } = this
      if (!start || !end) {
        return ''
      }
      const startMonth = this.monthFormatter(start)
      const endMonth = this.monthFormatter(end)
      const suffixMonth = startMonth === endMonth ? '' : endMonth
      const startYear = start.year
      const endYear = end.year
      const suffixYear = startYear === endYear ? '' : endYear
      const startDay = start.day + this.nth(start.day)
      const endDay = end.day + this.nth(end.day)
      switch (this.type) {
        case 'month':
        return `${startMonth} ${startYear}`
        case 'week':
        case '4day':
        return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`
        case 'day':
        return `${startMonth} ${startDay} ${startYear}`
      }
      return ''
    },
    monthFormatter () {
      return this.$refs.calendar.getFormatter({
        timeZone: 'UTC', month: 'long',
      })
    }
  },
  methods: {
    async getCalendar (selectNow = null) {
      const CalendarObj = await dbRealTime.ref('Calendars').once('value') 
      const nameCalendar = CalendarObj.val();
    
      //add to calendar list 
      this.typeCalendar = []
      for(name in nameCalendar){
        this.typeCalendar.push(name);
      }  
      if(selectNow == null){
        this.SelectNow = this.typeCalendar[0]
      }else{
        this.SelectNow = selectNow
      }
     
      let EventNowObj = CalendarObj.val()[this.SelectNow]
      const events = [] 
      if(typeof EventNowObj === 'object'){
        //add to event list
        for(event in EventNowObj){
          if(typeof EventNowObj[event] === 'object'){
            events.push(EventNowObj[event])
          }
        }
      }
      
      this.events = events
      console.log(this.events)
    },
    async addEvent () {
      if (this.name && this.start && this.end) {
        const id = Random();
        console.log(this.color)
        await dbRealTime.ref(`Calendars/${this.SelectNow}/event${id}`).set({
          name: this.name,
          details: this.details,
          start: this.start,
          end: this.end,
          color: this.color,
          id: id
        })
        this.getCalendar(this.SelectNow)
        this.name = '',
        this.details = '',
        this.start = '',
        this.end = ''
      } else {
        alert('You must enter event name, start, and end time')
      }
    },
    async addCalendar(){
      if (this.calendarName) {
        await dbRealTime.ref(`Calendars/${this.calendarName}`).set({
          event: 'hello'
        }) 
        this.getCalendar(this.calendarName)
        this.calendarName = ''
        this.dialogCalendar = false
      } else {
        alert('You must enter event name, start, and end time')
      }
    },
    async deleteCalendar(value){
      if(this.typeCalendar.length > 1){
        await dbRealTime.ref(`Calendars/${value}`).remove()
        this.getCalendar()
      }else{
        alert('вы не можете удалить свой последний календарь')
      }
      
    },
    editEvent (ev) {
      this.currentlyEditing = ev.id
    },
    async updateEvent (ev) {
      await dbRealTime.ref(`Calendars/${this.SelectNow}/event${this.currentlyEditing}`).update({
        details: ev.details
      })
      this.getCalendar(this.SelectNow)
      this.selectedOpen = false,
      this.currentlyEditing = null
    },
    async deleteEvent (ev) {
      await dbRealTime.ref(`Calendars/${this.SelectNow}/event${ev}`).remove()
      this.selectedOpen = false
      this.getCalendar(this.SelectNow)
    },
    setDialogDate( { date }) {
      this.dialog = true
      this.start = date
    },
    viewDay ({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor (event) {
      return event.color
    },
    setToday () {
      this.focus = this.today
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    
    showEvent ({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        setTimeout(() => this.selectedOpen = true, 10)
      }
      if (this.selectedOpen) {
        this.selectedOpen = false
        setTimeout(open, 10)
      } else {
        open()
      }
      nativeEvent.stopPropagation()
    },
    updateRange ({ start, end }) {
      this.start = start
      this.end = end
    },
    nth (d) {
      return d > 3 && d < 21
      ? 'th'
      : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
    }
  }
}
</script>
